import './App.css';
import About from './About.js'
import Project from './Project.js'
import { useState } from 'react';


function App() {
  let projects = [
    { title: 'Escape', description: 'Design... patterns...' },
    { title: 'Mushroom Hunting', description: 'I pretend I know about ML' },
    { title: 'WhatDo?', description: 'I have suffered through AWS' }
  ]

  const [viewProjects, setViewProjects] = useState(false)

  return (
    <div className='app'>
      <div className='nav'>
        <ul>
          <li>
            <h1>Charlotte Clark</h1>
          </li>
          <div className='links'>
            <li>
              <button onClick={() => setViewProjects(false)}>about</button>
            </li>
            <li>
              <button onClick={() => setViewProjects(true)}>projects</button>
            </li>
            <li>
              <a target='_blank' href='../public/resume.pdf'>resume (pdf)</a>
            </li>
          </div>
        </ul>
      </div>
      <div className='contentarea'>
        {viewProjects
          ? projects.map((project, idx) => { return <Project key={`${project.title}-${idx}`} title={project.title} description={project.description} /> })
          : <About />
        }
      </div>
    </div>
  );
}

export default App;
